.cooperation {
  background-color: $grey4;

  &__inner {
    padding-top: 27px;
    padding-bottom: 26px;
    display: flex;
    z-index: 10;
    justify-content: space-between;
  }

  &__title {
    font-size: 16px;
    line-height: 20px;
    font-weight: 400;
    text-transform: uppercase;
    color: $black1;
    width: 50%;
    margin: 0;
  }

  &__adress {
    font-family: $arial-font;
    font-size: 14px;
    line-height: 20px;
    width: 50%;
  }

  &__street {
    margin: 0;
  }

  &__city {
    margin: 0;
  }

  &--index {
    background: $white;
  }

  &__map {
    background: $white url("../../img/bg-map-mobile@1x.png") center no-repeat;
    position: relative;
    height: 363px;
    z-index: 5;

    &::before {
      content: "";
      position: absolute;
      background: url("../../img/map-pin.png") center no-repeat;
      background-size: 55px 53px;
      width: 55px;
      height: 53px;
      top: 158px;
      z-index: -5;
      left: calc(50% - 28px);
    }
  }
}

.map {
  border: 0;
  width: 100%;
  height: 362px;
  margin-bottom: -2px;
  z-index: 10;
}

@media (min-width: $tablet-width) {
  .cooperation {
    &__inner {
      padding: 64px 41px 57px;
    }

    &__title {
      width: 160px;
      font-size: 20px;
      line-height: 25px;
    }

    &__adress {
      font-size: 16px;
      width: 170px;
      text-align: right;
    }

    &__street {
      margin-bottom: 19px;
    }

    &--index {
      background: $white;
    }

    &__map {
      background: $map url("../../img/bg-map-tablet@1x.png");
      position: relative;
      width: 100%;
      height: 396px;

      &::before {
        content: "";
        position: absolute;
        background-image: url("../../img/map-pin.png");
        width: 113px;
        height: 106px;
        top: 170px;
        left: 333px;
      }
    }
  }

  .map {
    height: 396px;
  }
}

@media (min-width: $desktop-width) {
  .cooperation {
    position: relative;

    &__inner {
      position: absolute;
      width: 570px;
      top: 95px;
      padding: 64px 77px 58px;
      background: $white;
    }

    &__title {
      width: 50%;
      font-size: 20px;
      line-height: 25px;
    }

    &__adress {
      width: 40%;
      text-align: left;
    }

    &__street {
      margin-bottom: 20px;
      width: 155px;
    }

    &--index {
      background: $white;
    }

    &__map {
      margin-bottom: -2px;
      background: $map url("../../img/bg-map-desktop@1x.png");
      position: relative;
      width: 100%;
      height: 400px;

      &::before {
        content: "";
        position: absolute;
        background: url("../../img/map-pin.png");
        width: 113px;
        height: 106px;
        top: 151px;
        right: 0;
      }
    }
  }

  .map {
    height: 400px;
    margin-bottom: -3px;
  }
}
